// ** Reactstrap Imports
import { Row, Col, Form, Button, Modal, Input, Label, ModalBody, ModalHeader } from 'reactstrap'
import { useForm, Controller } from 'react-hook-form'
import { Check,  X } from 'react-feather'
import { selectThemeColors } from '@utils'
import { useEffect, useState } from 'react'
import { useParams, useHistory } from 'react-router-dom'
import {useDispatch, useSelector } from 'react-redux'
import Select from 'react-select'
import { toast, Slide } from 'react-toastify'
import { getSingleDeliveryCode, updateDeliveryCode } from './store/deliverySlice'
const ToastContent = ({ name}) => (
  <>
    <div className='toastify-header'>
      <div className='title-wrapper'>
        <h6 className='toast-title fw-bold'>You have successfully updated District : {name.toUpperCase()}</h6>
      </div>
    </div>
  </>
)

const Edit = () => {
    // ** State
    const id = useParams()
    const dispatch = useDispatch()
    const store = useSelector(state => state.customers)
    const [selectedStatus, setSelectedStatus] = useState(false)
    const history = useHistory()
      // ** Hook
    const {
    reset,
    control,
    setValue,
    setError,
    handleSubmit,
    formState: { errors }
    } = useForm({
    defaultValues: {
      pinCode: '',
      district: '',
      countryName: ''
  }
    })
    const onSubmit = data => {
      console.log(Object.values(data))
      if (Object.values(data).every(field => field.length > 0)) {
        console.log(data)
        console.log(selectedStatus.value)
        dispatch(updateDeliveryCode({
          ...store.message,
          ...data, 
          status : selectedStatus.value
        })).then(res => {
          history.push("/delivery")
          console.log(res)
          toast.success(
            <ToastContent name={res.payload.district || res.payload.username || 'John Doe'} />,
            { icon: false, transition: Slide, hideProgressBar: true, autoClose: 2000 }
          )
        })
      } else {
        for (const key in data) {
          if (data[key].length === 0) {
            setError(key, {
              type: 'manual'
            })
          }
        }
      }
    }
    const statusOptions = [
      { value: true, label: 'Active' },
      { value: false, label: 'Inactive' }
    ]
    const handleReset = () => {
        reset({
            name: store.message.name,
            email: store.message.email,
            contactNumber: store.message.contactNumber,
            address: store.message.address
        })
      }

      useEffect(() => {
        dispatch(getSingleDeliveryCode(id.id)).then((res) => {
          setValue('pinCode', res.payload.pinCode) 
          setValue('district', res.payload.district) 
          setValue('countryName', res.payload.countryName) 
          setSelectedStatus(res.payload.status)  
        })
      }, [dispatch, setValue, id.id])

    return ( 
        <>
       <div className='text-center mb-2'>
            <h1 className='mb-1'>Edit Information</h1>
          </div>
          <Form onSubmit={handleSubmit(onSubmit)}>
            <Row className='gy-1 pt-75'>
            <Col md={6} xs={12}>
                <Label className='form-label' for='pinCode'>
                  PinCode
                </Label>
                <Controller
                  control={control}
                  type='pinCode'
                  id='pinCode'
                  name='pinCode'
                  render={({ field }) => (
                    <Input {...field} id='pinCode' placeholder='46512' invalid={errors.pinCode && true} />
                  )}
                />
              </Col>
              <Col md={6} xs={12}>
                <Label className='form-label' for='district'>
                  District
                </Label>
                <Controller
                  control={control}
                  type='district'
                  id='district'
                  name='district'
                  render={({ field }) => (
                    <Input {...field} id='district' placeholder='' invalid={errors.district && true}/>
                  )}
                />
              </Col>
              <Col md={6} xs={12}>
                <Label className='form-label' for='countryName'>
                  Country Name
                </Label>
                <Controller
                  control={control}
                  type='countryName'
                  id='countryName'
                  name='countryName'
                  render={({ field }) => (
                    <Input {...field} id='countryName' placeholder='' invalid={errors.countryName && true}/>
                  )}
                />
              </Col>
              <Col md={6} xs={12}>
                <Label className='form-label' for='status'>
                  Status:
                </Label>
                <Select
                  id='status'
                  isClearable={false}
                  className='react-select'
                  classNamePrefix='select'
                  options={statusOptions}
                  theme={selectThemeColors}
                  value={statusOptions[statusOptions.findIndex(i => i.value === selectedStatus)]}
                  onChange={option => setSelectedStatus(option)}
                />
              </Col>
              
              <Col xs={12} className='text-start mt-2 pt-50'>
                <Button type='submit' className='me-1' color='primary'>
                  Submit
                </Button>
                <Button
                  type='reset'
                  color='secondary'
                  outline
                  onClick={() => {
                    handleReset()
                    history.push("/delivery")
                  }}
                >
                  Discard
                </Button>
              </Col>
            </Row>
          </Form>
        </>
     )
}
 
export default Edit